<script>
// @ is an alias to /src 
import FETCHING from "../../../library/fetching";
import UMUM from "../../../library/umum";
import { Notify } from 'quasar' 


export default {
    data() {
        return {
            mdl_add         : false,
            mdl_edit        : false,
            mdl_del         : false,
            mdl_list        : false,
            loading         : false,
            
            list_mdl_add    : false,
            list_mdl_edit   : false,
            list_mdl_del    : false,

            date            :'',

            halaman         : 'Retribusi',
            options         : ['data1', 'data2', 'data3', 'data4'],
            model           : '',
            text            : '',
            FETCHING        : FETCHING,
            UMUM            : UMUM,
            form : {
                id          : '',
                uraian      : '',
                alamat      : '',
                prov        : '74',
                kab         : '7405',
                kec         : '',
                des         : '',
            },
            form_los:{ 
                id       :'', 
                uraian   :'', 
                id_ur    :'', 
                createAt :'', 
                editAt   :'', 
                createBy :''
            },

            list_los:[],
            listData: [],
        }
    },
 

    methods: {
        filterFn(val, update) {
            if (val === '') {
                update(() => {
                    console.log(val);
                })
                return
            }
        },

        getView() {
            console.log(this.date);
            // this.loading=true
            // 2023-05-17

            // fetch(this.$store.state.url.M_UNIT_RETRIBUSI + 'view', {
            //     method: "POST",
            //     headers: {
            //         "content-type": "application/json",
            //         authorization: "kikensbatara " + localStorage.token
            //     },
            //     // body: JSON.stringify(this.form)
            // })
            //     .then(res => res.json())
            //     .then(res_data => {
            //         this.listData = res_data
            //         this.loading=false
            //         console.log(res_data);

            //     });
        },

        addData() {
            fetch(this.$store.state.url.M_UNIT_RETRIBUSI + 'addData', {
                method: "POST",
                headers: {
                    "content-type": "application/json",
                    authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
                .then(res => res.json())
                .then(res_data => {
                    this.getView()
                    this.berhasil()
                });
        },
        editData() {
            // console.log(this.form);
            fetch(this.$store.state.url.M_UNIT_RETRIBUSI + 'editData', {
                method: "POST",
                headers: {
                    "content-type": "application/json",
                    authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
                .then(res => res.json())
                .then(res_data => {
                    this.getView()
                    this.berhasil()
                });
        },
        removeData() {
            // console.log(this.form.id);
            fetch(this.$store.state.url.M_UNIT_RETRIBUSI + 'removeData', {
                method: "POST",
                headers: {
                    "content-type": "application/json",
                    authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
                .then(res => res.json())
                .then(res_data => {
                    this.getView()
                    this.berhasil()
                });
        },

        getData(data) {
            // console.log(data);
            this.form.id        = data.id
            this.form.uraian    = data.uraian
            this.form.alamat    = data.alamat
            this.form.prov      = data.prov
            this.form.kab       = data.kab
            this.form.kec       = data.kec
            this.form.des       = data.des
        },

        berhasil() {
            Notify.create({
                message: 'BERHASIL',
                // caption: '5 minutes ago',
                icon: 'check',
                color: 'secondary',
                position: 'center',
                timeout: 2000
            })
        },


        // =============================== LOS DATA ============================
        async los_get (id){
            if (id == '' ||id == 0 ||id == undefined ||id == null) {
                console.log('nothing');
            } else {
                console.log('go get some data');
                this.list_los = await FETCHING.FETCH_POST(this.$store.state.url.M_LIST_UNIT_RETRIBUSI + "view", { id: id })
            }
        },
        async los_add(){
            console.log(this.form.id);
            console.log(this.form_los);
            var a = await FETCHING.FETCH_POST(this.$store.state.url.M_LIST_UNIT_RETRIBUSI + "addData", { 
                uraian : this.form_los.uraian,
                id_ur  : this.form.id
            })
            console.log(a);
            this.getView()
            this.los_get(this.form.id) 
        },
        los_update(){},
        los_delete(){},
        los_select(data){ 
            console.log(data);
                this.form_los.id       = data.id
                this.form_los.uraian   = data.uraian
                this.form_los.id_ur    = data.id_ur
                this.form_los.createAt = data.createAt
