<template>
    <div class="">


        <div class="row q-mt-md q-col-gutter-xs">
            <div class="col col-xs-12 col-sm-12 col-md-12 self-center q-px-md">
                <div class="q-mx-md text-h6 text-center text-weight-bolder">{{ halaman }}</div>
                <!-- <q-separator class="" color="orange" inset /> -->
            </div>

            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Masyarakat / OPD / Sub OPD" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Simple filter" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-3 q-px-md self-center">
                <q-input class="self-center" filled v-model="text" label="Pencarian" :dense="true">
                    <template v-slot:append>
                        <q-icon v-if="text !== ''" name="close" @click="text = ''" class="cursor-pointer" />
                        <q-icon name="search" />
                    </template>
                </q-input>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-1 q-px-md self-center">
                <q-btn round @click="mdl_add = true" color="black" icon="add" />
            </div>
        </div>







        <div class="row q-mt-md q-mx-md">
            <!-- <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <div class="text-h6">Halaman</div>
            </div> -->

            <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <q-markup-table class="my-sticky-dynamic" flat bordered separator="cell">
                    
                    <thead>
                        <tr class="bg-blue-1">
                            <th class="text-center" style="width:10%;">No</th>
                            <th class="text-center" style="width:40%;">Nama</th>
                            <th class="text-center" style="width:10%;">Username</th>
                            <th class="text-center" style="width:10%;">Email</th>
                            <th class="text-center" style="width:10%;">Unit Retribusi</th>
                            <th class="text-center" style="width:10%;">Ganti Password</th>
                            <th class="text-center" style="width:10%;">Act</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in listData" :key="item.id">
                            <td class="text-center">{{index+1}}.</td>
                            <td class="text-left text-no-wrap" > <span style="font-size:12px;">{{item.nama}}</span>  <br> <span class="text-brown-5" style="font-size:11px;">{{item.alamat}}</span> </td>
                            <td class="text-left" style="font-size:10px;">{{item.username}} </td>
                            <td class="text-left" style="font-size:10px;">{{item.email}}</td>
                            <td class="text-left" style="font-size:10px;">{{item.uraian_kec}}</td>
                            <td class="text-center" style="font-size:10px;">
                                <q-btn push color="green" icon="key" class="text-black" size="sm" @click="mdl_edit=true, getData(item)">
                                    <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Ubah </q-tooltip>                                            
                                </q-btn>
                            </td>
                            <td class="text-center">
                                <div>
                                    <q-btn-group push>
                                        <!-- <q-btn push color="primary" icon="edit" size="sm"/> -->
                                        <q-btn push color="warning" icon="edit" class="text-black" size="sm" @click="mdl_edit=true, getData(item)">
                                            <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Ubah </q-tooltip>                                            
                                        </q-btn>
                                        <q-btn push color="negative" icon="delete" size="sm" @click="mdl_del=true, getData(item)">
                                            <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Hapus </q-tooltip>
                                        </q-btn>
                                    </q-btn-group> 
                                </div>                                
                            </td>
                        </tr>
                    </tbody>
                </q-markup-table>
            </div>
            
 


        </div>

        <q-dialog persistent v-model="mdl_add" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-cyan">
                    <div class="text-overline">INPUT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="addData()">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">

                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.nama" label="nama" :dense="true"/>   
                        </div>                                    
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.username" label="username" :dense="true"/>   
                        </div>                                                
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.email" label="email" :dense="true"/>   
                        </div>                                    
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.hp" label="hp" :dense="true"/>   
                        </div> 
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.password" label="Password" :dense="true"/>   
                        </div>
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.password2" label="Confirm Password" :dense="true"/>   
                        </div>                                    

                        <div v-if="form.password != form.password2">
                            <!-- password tidak sama -->
                            <q-banner dense inline-actions class="text-white bg-red">
                                Password Tidak Sama. 
                            </q-banner>
                            
                        </div>

                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <q-input class="self-center" filled v-model="form.menu_klp" label="menu_klp" :dense="true"/>   
                        </div>                 

                            <div class="col-md-12 col-sm-12 col-xs-12"> 
                                <q-select filled v-model="form.unit_retribusi" 
                                    use-input 
                                    input-debounce="0" 
                                    label="unit_retribusi"  
                                    :options="$store.state.L_RETRIBUSI" 
                                    option-value="id"
                                    option-label="uraian" 
                                    emit-value
                                    map-options         
                                    @filter="cariUnitRet" :dense="true" 
                                    >

                                    <template v-slot:option="scope">
                                        <q-item v-bind="scope.itemProps"> 
                                            <q-item-section class="text-grey">
                                            <q-item-label class="text-black" style="font-size: 12px;"> <b>{{ scope.opt.uraian }}</b> </q-item-label>
                                            <q-item-label caption style="font-size: 10px;">Prov: {{ scope.opt.uraian_prov }}</q-item-label>
                                            <q-item-label caption style="font-size: 10px;">Kab : {{ scope.opt.uraian_kab }}</q-item-label>
                                            <q-item-label caption style="font-size: 10px;">Kec : {{ scope.opt.uraian_kec }}</q-item-label>
                                            <q-item-label caption style="font-size: 10px;">Des : {{ scope.opt.uraian_des }}</q-item-label>
                                            </q-item-section>
                                        </q-item>
                                    </template>

 
                                </q-select> 
                            </div>                        
                      
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Tambah" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog persistent v-model="mdl_edit" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-amber">
                    <div class="text-overline">EDIT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="editData(form)">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">
                            <div class="col-md-6 col-sm-12 col-xs-12">
                                <q-input class="self-center" filled v-model="form.uraian" label="Nama Kantor" :dense="true"/>  
                            </div>                        
                            <div class="col-md-6 col-sm-12 col-xs-12">
                                <q-input class="self-center" filled v-model="form.alamat" label="Alamat" :dense="true"/>   
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
                          
                                <q-select filled v-model="form.prov"   
                                    :options="$store.state.L_PRO"
                                    @filter="cariProv" :dense="true" 
                                    option-value="id"
                                    option-label="uraian"
                                    label="Provinsi"
                                    use-input 
                                    input-debounce="0"
                                    emit-value
                                    map-options        
                                    disable                                
                                    > 
                                    <template v-slot:no-option>
                                        <q-item>
                                            <q-item-section class="text-grey">
                                                No results
                                            </q-item-section>
                                        </q-item>
                                    </template>
                                </q-select>
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
                  
                                <q-select filled v-model="form.kab" 
                                    use-input 
                                    input-debounce="0" 
                                    label="Kabupaten/Kota" 
                                    :options="$store.state.L_KAB" 
                                    option-value="id"
                                    option-label="uraian" 
                                    emit-value
                                    map-options         
                                    @filter="cariKab" :dense="true"   
                                    disable
                                     
                                    >
                                    <template v-slot:no-option>
                                        <q-item>
                                            <q-item-section class="text-grey">
                                                No results
                                            </q-item-section>
                                        </q-item>
                                    </template>
                                </q-select>
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
             
                                <q-select filled v-model="form.kec" 
                                    use-input 
                                    input-debounce="0" 
                                    label="Kecamatan" 
                                    :options="$store.state.L_KEC" 
                                    option-value="id"
                                    option-label="uraian" 
                                    emit-value
                                    map-options         
                                    @filter="cariKec" :dense="true"   
                                    >
                                    <template v-slot:no-option>
                                        <q-item>
                                            <q-item-section class="text-grey">
                                                No results
                                            </q-item-section>
                                        </q-item>
                                    </template>
                                </q-select>
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
          
                                <q-select filled v-model="form.des" 
                                    use-input 
                                    input-debounce="0" 
                                    label="Desa/Kelurahan"  
                                    :options="$store.state.L_DES" 
                                    option-value="id"
                                    option-label="uraian" 
                                    emit-value
                                    map-options         
                                    @filter="cariDes" :dense="true"                                   
                                    
                                    >
                                    <template v-slot:no-option>
                                        <q-item>
                                            <q-item-section class="text-grey">
                                                No results
                                            </q-item-section>
                                        </q-item>
                                    </template>
                                </q-select>
                            </div>                        
                      
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Tambah" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog v-model="mdl_del" persistent>
            <q-card>
                <q-card-section class="row items-center">
                <q-avatar icon="signal_wifi_off" color="primary" text-color="white" />
                <span class="q-ml-sm">You are currently not connected to any network.</span>
                </q-card-section>

                <form @submit.prevent="removeData(form)">
                    <q-card-actions align="right">
                        <q-btn flat label="Hapus" type="submit" color="primary" />
                        <q-btn flat label="Cancel" color="negative" v-close-popup />
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>        
        
    </div>
</template>

<script>
// @ is an alias to /src 
import FETCHING from "../../../library/fetching"; 
import { Notify } from 'quasar'


export default {
    data() {
        return {
            mdl_add: false,
            mdl_edit: false,
            mdl_del: false,
            halaman: 'Registrasi',
            options: ['data1', 'data2', 'data3', 'data4'],
            model: '',
            text: '',
            FETCHING : FETCHING,
            form:{
                id              :'', 
                nama            : '',
                username        : '',
                hp              : '',
                email           : '',
                password        : '',
                password2       : '',
                unit_retribusi  : '', 
                menu_klp        : '', 
            },

            listData:[],

            page_limit : 8,
            page_first: 1,
            page_last: 0,
            cari_value: "",
        }
    },

    methods: {
        filterFn(val, update) {
            if (val === '') {
                update(() => {
                    console.log(val); 
                })
                return
            }
        },

        getView(){
            fetch(this.$store.state.url.URL_REGISTER+'view', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify({
                    data_ke: this.page_first,
                    page_limit : this.page_limit,
                    cari_value: this.cari_value
                })
            })
            .then(res => res.json())
            .then(res_data => { 
                this.listData = res_data.data
                console.log(res_data.data);
                 
            });                
        },

        addData(){ 
            fetch(this.$store.state.url.URL_REGISTER+'signup', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
            }); 
        },
        editData(){ 
            // console.log(this.form);
            fetch(this.$store.state.url.URL_REGISTER+'editData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
            }); 
        },
        removeData(){ 
            // console.log(this.form.id);
            fetch(this.$store.state.url.URL_REGISTER+'removeData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
                this.mdl_del = false 
            }); 
        },

        getData(data){
            // console.log(data); 
            this.form.id              = data.id
            this.form.nama            = data.nama
            this.form.username        = data.username
            this.form.hp              = data.hp
            this.form.email           = data.email
            this.form.password        = data.password
            this.form.password2       = data.password2
            this.form.unit_retribusi  = data.unit_retribusi
            this.form.menu_klp        = data.menu_klp
            this.cariUnitRet()
        },

        berhasil(){
            Notify.create({
            message: 'BERHASIL',
            // caption: '5 minutes ago',
            icon:'check',
            color: 'secondary' ,
            position:'center',
            timeout: 2000 })
        },




        // ================================================ FILTER ================================================ 
        cariUnitRet(val, update, abort){
            update(async () => {
                console.log(val);
                if (val === '') {}
                else{
                    fetch(this.$store.state.url.M_UNIT_RETRIBUSI+'list', {
                        method: "POST",
                        headers: {
                        "content-type": "application/json",
                        authorization: "kikensbatara " + localStorage.token
                        },
                        body: JSON.stringify({
                            cari: val
                        })
                    })
                    .then(res => res.json())
                    .then(res_data => { 
                        this.$store.state.L_RETRIBUSI = res_data  
                    });                
                }
            })

        },

        // cariUnitRet : function (val, update, abort) {
        //       update(async () => {
        //         console.log(val);
        //         if (val === '') {}
        //         else {
        //           this.$store.state.L_RETRIBUSI = await FETCHING.FETCH_POST(this.$store.state.url.M_UNIT_RETRIBUSI + "list", {cari : val})
        //         //   console.log(this.form.prov);
        //         //   this.getView()
        //         }
        //       })
        // },
        
        cariProv : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_PRO = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "provinsi", {cari : val})
                //   console.log(this.form.prov);
                //   this.getView()
                }
              })
        },
        cariKab : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KAB = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kabupaten", {cari : val, prov_id:this.form.prov  })
                }
              })
        },
        cariKec : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KEC = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kecamatan", {cari : val, kab_id:this.form.kab  })
                }
              })
        },
        cariDes : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_DES = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "desa", {cari : val, kec_id:this.form.kec  })
                }
              })
        },
        // ================================================ FILTER ================================================ 
    },
    mounted() {
        this.getView()
        
    },

}
</script>
