<script>
// @ is an alias to /src 
import FETCHING from "../../../library/fetching";
import { Notify } from 'quasar' 


export default {
    data() {
        return {
            mdl_add         : false,
            mdl_edit        : false,
            mdl_del         : false,
            mdl_cash         : false,
            mdl_list        : false,
            loading         : false,
            mdl_konfirmasi_pembayaran :false,
            
            list_mdl_add    : false,
            list_mdl_edit   : false,
            list_mdl_del    : false,

            date            :'',

            halaman         : 'Bayar Retribusi',
            options         : ['data1', 'data2', 'data3', 'data4'],
            model           : '',
            text            : '',
            FETCHING        : FETCHING,
            form : {
                id          : '',
                uraian      : '',
                alamat      : '',
                prov        : '74',
                kab         : '7405',
                kec         : '',
                des         : '',
            },
            form_los:{ 
                id       :'', 
                uraian   :'', 
                id_ur    :'', 
                createAt :'', 
                editAt   :'', 
                createBy :''
            },

            list_los:[],
            listData: [],
        }
    },
 

    methods: {
        filterFn(val, update) {
            if (val === '') {
                update(() => {
                    console.log(val);
                })
                return
            }
        },

        onDecode (decodedString) {
            // ...
            if (decodedString != ''|| decodedString != undefined || decodedString != null) {
                var a = JSON.parse(decodedString)
                console.log(a)
                var saldo = a[0].saldo
                if (saldo>= 10000) {
                    // alert("Saldo cukup. lakukan transaksi?" )
                    // this.konfirmasi_pembayaran()
                    this.mdl_konfirmasi_pembayaran = true
                }else{
                    this.mdl_del = true
                    // alert("Saldo Tidak cukup. lakukan TopUp" )
                }
                // alert(decodedString)
            } else if(decodedString=='1'){
                alert('berhasil membayar')
            }
        },        

        getView() {
            console.log(this.date); 
        },

        addData() { 
        },
        editData() { 
        },
        removeData() { 
        },

        getData(data) {
            // console.log(data);
            this.form.id        = data.id
            this.form.uraian    = data.uraian
            this.form.alamat    = data.alamat
            this.form.prov      = data.prov
            this.form.kab       = data.kab
            this.form.kec       = data.kec
            this.form.des       = data.des
        },

        berhasil() {
            Notify.create({
                message: 'BERHASIL',
                // caption: '5 minutes ago',
                icon: 'check',
                color: 'secondary',
                position: 'center',
                timeout: 2000
            })
        },
        konfirmasi_pembayaran() {
            Notify.create({
                message     : 'Saldo cukup. lakukan transaksi.?',
                color       : 'primary',
                // avatar: 'https://cdn.quasar.dev/img/boy-avatar.png',
                position    : 'center',
                // icon        : 'check',
                actions: [
                    { label: 'Ya', color: 'yellow', handler: () => { 
                        alert('Berhasil membayar')
                    }},
                    { label: 'Batal', color: 'white', handler: () => { 
                        alert('Batal Memproses')
                    }}
                ]  
            })
        },


        // =============================== LOS DATA ============================
        async los_get (id){
            if (id == '' ||id == 0 ||id == undefined ||id == null) {
                console.log('nothing');
            } else {
                console.log('go get some data');
                this.list_los = await FETCHING.FETCH_POST(this.$store.state.url.M_LIST_UNIT_RETRIBUSI + "view", { id: id })
            }
        },
        async los_add(){
            console.log(this.form.id);
            console.log(this.form_los);
            var a = await FETCHING.FETCH_POST(this.$store.state.url.M_LIST_UNIT_RETRIBUSI + "addData", { 
                uraian : this.form_los.uraian,
                id_ur  : this.form.id
            })
            console.log(a);
            this.getView()
            this.los_get(this.form.id) 
        },
        los_update(){},
        los_delete(){},
        los_select(data){ 
            console.log(data);
                this.form_los.id       = data.id
                this.form_los.uraian   = data.uraian
                this.form_los.id_ur    = data.id_ur
                this.form_los.createAt = data.createAt
                this.form_los.editAt   = data.editAt
                this.form_los.createBy = data.createBy
        },
        // =============================== LOS DATA ============================




        // ================================================ FILTER ================================================ 
        cariProv: function (val, update, abort) {
            update(async () => {
                console.log(val);
                if (val === '') { }
                else {
                    this.$store.state.L_PRO = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "provinsi", { cari: val })
                    //   console.log(this.form.prov);
                    //   this.getView()
                }
            })
        },
        cariKab: function (val, update, abort) {
            update(async () => {
                console.log(val);
                if (val === '') { }
                else {
                    this.$store.state.L_KAB = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kabupaten", { cari: val, prov_id: this.form.prov })
                }
            })
        },
        cariKec: function (val, update, abort) {
            update(async () => {
                console.log(val);
                if (val === '') { }
                else {
                    this.$store.state.L_KEC = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kecamatan", { cari: val, kab_id: this.form.kab })
                }
            })
        },
        cariDes: function (val, update, abort) {
            update(async () => {
                console.log(val);
                if (val === '') { }
                else {
                    this.$store.state.L_DES = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "desa", { cari: val, kec_id: this.form.kec })
                }
            })
        },
        // ================================================ FILTER ================================================ 
    },
    mounted() {
        this.getView()
        // this.getView()
        this.date = new Date().toJSON().slice(0, 10);
        console.log(this.date);

    },

}
</script>
